@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap);
* {
  font-family: "Noto Sans KR", sans-serif;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(248, 249, 250);
}
li {
  list-style: none;
}
a,
ul,
li ol,
button,
input {
  margin: 0;
  padding: 0;
  outline-style: none;
  text-decoration: none;
  border: none;
  background: none;
  color: black;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Header{display:flex;max-width:1080px;margin:0 auto;padding-bottom:20px}.Header h1{font-size:1rem;margin:0;line-height:1rem;position:fixed;top:10px;left:20px;z-index:9}.Header h1 a{font-family:KoPubDotumPBA, sans-serif;display:block}.Header h1 span{display:block;font-family:KoPubDotumPBA, sans-serif}.Header h1 span::after{content:"";width:5px;height:5px;background:black;display:inline-block;border-radius:50%;margin-left:3px}.Header nav{position:fixed;left:50%;transform:translateX(-50%);z-index:8}.Header nav ul{display:flex;justify-content:center}.Header nav ul li{font-size:0.8rem;margin-right:30px}.Header nav ul li:last-of-type{margin-right:0}.Header nav ul li a{color:rgba(0,0,0,0.4);line-height:47px;display:block;font-family:KoPubDotumPBA, sans-serif}.Header nav ul li a:hover{color:#000}

.Main{max-width:1080px;margin:0 auto;text-align:center;padding-top:10rem}@media (max-width: 480px){.Main{padding-top:7rem}}

.HomeContent{width:20rem;margin:1rem;border-radius:4px;overflow:hidden;box-shadow:rgba(0,0,0,0.04) 0px 4px 16px 0px;text-align:left;transition:0.4s}.HomeContent:hover{transform:translateY(-15px)}.HomeContent a{display:flex;flex-direction:column}.HomeContent a h3{order:2;font-size:1rem;line-height:1.5;text-overflow:ellipsis;white-space:nowrap;margin:0 0 0.25rem;padding:1rem 1rem 0}.HomeContent a .thumbnail{width:100%;display:block;position:relative;box-sizing:border-box;padding-top:65%;order:1}.HomeContent a .thumbnail img{width:100%;height:100%;position:absolute;top:0px;left:0px;display:block;object-fit:cover}.HomeContent a .content{order:3;padding:0 1rem;margin:0;word-break:break-word;overflow-wrap:break-word;font-size:0.875rem;line-height:1.5;height:3.9375rem;display:-webkit-box;-webkit-line-clamp:3;-webkit-box-orient:vertical;text-overflow:ellipsis;color:#495057;margin:0px 0px 1.5rem;overflow:hidden}.HomeContent .date{order:4;padding:0 1rem 1rem;margin:0;font-size:0.75rem;line-height:1.5;color:#868e96}

.Home{display:flex;flex-wrap:wrap;justify-content:center}.Home h2{width:1px;height:1px;overflow:hidden;font-size:0}

.ProjectList{width:100%;margin:0 auto;text-align:left;overflow:hidden}.ProjectList h2{margin:0;padding:0 0 3rem;text-align:center}.ProjectListItem{max-width:700px;margin:0 auto;padding:1rem}.ProjectListItem li{padding:24px 0 27px;border-bottom:1px solid #eee;position:relative;transition:0.4s;overflow:hidden}.ProjectListItem li:hover{transform:translateY(15px)}.ProjectListItem li a{display:block}.ProjectListItem li a strong{font-size:1.5rem;letter-spacing:-1px;white-space:nowrap;font-weight:300;color:#333}.ProjectListItem li a p{width:540px;font-size:0.9rem;line-height:21px;overflow:hidden;display:-webkit-box;max-height:43px;padding-top:5px;-webkit-line-clamp:2;-webkit-box-orient:vertical;text-overflow:ellipsis;margin:0;color:#959595}.ProjectListItem li a span{display:block;font-size:0.8rem;line-height:21px;padding-top:21px;color:#959595}.ProjectListItem li a .thumBox{position:absolute;right:0;top:50%;transform:translateY(-50%)}.ProjectListItem li a .thumBox img{width:120px}@media (max-width: 1000px){.ProjectListItem li a strong{font-size:1.2rem}.ProjectListItem li a p{font-size:0.7rem}.ProjectListItem li a span{font-size:0.6rem}}

.Project{max-width:600px;box-sizing:border-box;padding:2rem;margin:0 auto;text-align:left;position:relative}.Project h2{text-align:center}.Project .pageLink{text-align:center;margin-bottom:1rem}.Project .pageLink a{border:1px solid #959595;padding:0.1rem 0.3rem;margin-right:20px;border-radius:3px;box-shadow:0px 3px 2px #959595}.Project .pageLink a:hover{box-shadow:0px 3px 2px}.Project .pageLink a:active{border:1px solid black}.Project .pageLink a:last-of-type{margin-right:0}.Project .slideBox{width:100%}.Project .slideBox .slider{width:300px;position:relative;margin:0 auto;overflow:hidden}.Project .slideBox .slider ul{display:flex;transition:0.5s}.Project .slideBox .slider ul li{max-width:600px;min-width:100%}.Project .slideBox .slider ul li img{width:100%;object-fit:cover}.Project .slideBox .arrowBtn{position:absolute;top:50%;left:0;transform:translateY(-50%);font-size:2.5rem;color:#959595;cursor:pointer}.Project .slideBox .arrowBtn:last-of-type{left:auto;left:initial;right:0}.Project .slideBox .arrowBtn:hover{color:black}.Project .contents p{margin:0;white-space:pre-wrap;font-size:0.8rem;line-height:21px;padding-top:21px;color:#959595}.Project .etc{position:absolute;top:-10px;left:0;display:flex;flex-direction:column;padding-left:2rem;letter-spacing:2px}.Project .etc button{font-size:1rem;text-align:left;cursor:pointer}.Project .etc button:hover::after{width:100%;transform:translate(-50%, -50%)}.Project .etc button::after{content:"";display:block;background:black;width:0%;height:1px;position:relative;left:50%;transform:translate(-50%, -50%);transition:0.3s}.Project .etc button svg{margin-right:7px}.Project .etc span{font-size:0.8rem;line-height:21px;padding-top:10px;color:#959595}

.About{max-width:1080px;margin:0 auto;padding:1rem 2.5rem;text-align:left}.About h2{font-size:2rem;margin:0 0 120px 0;text-align:center}.About dl{max-width:600px;margin:50px auto;word-break:keep-all}.About dl dt{font-weight:400;font-size:1rem;color:#111;margin:0;padding:0 0 4px 0}.About dl dd{margin:0}.About dl dd p{font-weight:300;font-size:1rem;font-size:0.8rem;letter-spacing:1px;margin:0;padding:8px 0 0 15px}.About .icon{text-align:left;font-size:3rem;position:fixed;top:50%;left:20px;transform:translateY(-50%)}@media (max-width: 800px){.About .icon{position:absolute;left:50%;top:auto;top:initial;transform:translateX(-50%)}}

